<template>
  <v-card color="#F2F4F4" class="white--text" outlined height="100vh">
    <v-container>
      <v-card-title class="text-h5" style="color: #273746">
        Datos del cliente
      </v-card-title>
      <v-card-text style="color: #273746">Datos Generales</v-card-text>

      <v-form ref="form"
        >p
        <v-text-field
          outlined
          v-model="item.tradename"
          label="Nombre Comercial"
          required
        ></v-text-field>

        <v-select
          outlined
          v-model="item.municipality"
          :items="municipalityItems"
          label="Municipio"
          required
        ></v-select>
        <v-dialog
          ref="dialog"
          v-model="modal"
          :return-value.sync="item.date"
          persistent
          width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              outlined
              v-model="item.date"
              label="Fecha"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="item.date" scrollable>
            <v-spacer></v-spacer>
            <v-btn text color="primary" @click="modal = false"> Cancel </v-btn>
            <v-btn text color="primary" @click="$refs.dialog.save(item.date)">
              OK
            </v-btn>
          </v-date-picker>
        </v-dialog>

        <v-text-field
          outlined
          v-model="item.whoAttendedUs"
          label="¿Quien nos atendió?"
          required
        ></v-text-field>
      </v-form>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="#273746" text @click="start()" disabled> Cancelar </v-btn>
        <v-btn outlined color="#273746" @click="update()"> Guardar </v-btn>
      </v-card-actions>
    </v-container>
  </v-card>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "ClientDetail",
  data() {
    return {
      id: "",
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      municipalityItems: [
        "Cozumel",
        "Isla Mujeres",
        "Benito Juárez",
        "Solidaridad",
        "Puerto Morelos",
        "Felipe Carrillo Puerto",
        "José María Morelos",
        "Lázaro Cárdenas",
        "Tulum",
        "Othón P. Blanco",
        "Bacalar",
      ],
      typeOfOpinionItems: ["Basico"],

      modal: false,
      dialog: false,
      tradename: "",
      municipality: "",
      newDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      whoAttendedUs: "",
      typeOfOpinion: "",
    };
  },
  computed: {
    ...mapState({
      item: (state) => state.customers.findCustomer,
    }),
  },
  mounted() {
    this.init();
  },
  created() {
    this.init();
  },
  methods: {
    ...mapActions({
      getItem: "customers/search",
      saveItem: "customers/save",
    }),
    update() {
      this.saveItem(this.item);
    },
    init() {
      this.getItem(Number(this.$route.params.id));
    },
  },
};
</script>